import Vue from "vue";
// import Router from "vue-router";   
import VueRouter from 'vue-router';

import clubs from "./clubs.vue";
import staff from "./staff.vue";
import calendar from "./calendar.vue";
import home from "./home.vue";

Vue.use(VueRouter);
// Vue.use(Router);


  const routes = [
    {
      path: "/",
      component: home
    },
    {
      path: "/clubs",
      // name: "clubs",
      component: clubs
    },
    {
      path: "/staff",
      // name: "staff",
      component: staff
    },
    {
      path: "/calendar",
      // name: "calendar",
      component: calendar
    }
  ]
  const router = new VueRouter({
  routes, // short for routes: routes
  mode: 'history', // Optional: uses clean URLs instead of the default 'hash' mode
    scrollBehavior (to, from, savedPosition) {
    // 1. If a previous position was saved (e.g., using the browser's back button),
    //    return that position. This preserves expected behavior for history navigation.
    if (savedPosition) {
      return savedPosition;
    } 
    
    // 2. Otherwise, always scroll to the top of the page.
    //    This returns an object telling the browser to scroll to x: 0, y: 0.
    else {
      return { x: 0, y: 0 };
    }
  }
});

export default router; // Export and import into main.js
